import glob from 'glob';
import * as path from 'path';

const DEFAULT_EXCLUDED_FOLDER = 'node_modules';
const DEFAULT_GLOB_OPTIONS = { ignore: '**/node_modules/**' };

export const globFilesMatches = (
    baseDir: string,
    filesPattern: string,
    excludedFolder: string = DEFAULT_EXCLUDED_FOLDER,
) => {
    try {
        const files = glob.sync(path.resolve(baseDir, filesPattern), DEFAULT_GLOB_OPTIONS);
        const filteredFiles = files.filter((file) => !file.includes(excludedFolder));
        return filteredFiles;
    } catch (error) {
        throw new Error('Error Glob Files');
    }
}
